$basic-font-size: 16px;
$header-color: black;
$button-color:#00acdc;
$card-width: 200px;
$container-width: $card-width * 4 + 48 * 3;

@mixin basic-text {
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
}

@mixin button-size {
  height: 40px;
  line-height: 40px;
}
